<template>
  <div>
    <loader v-if="loading"></loader>
    <Map>

    </Map>
  </div>

</template>

<script>
    import msgs from "../utils/messages";
    import Loader from "../components/Loader"
    import Map from '../views/Map'
    import axios from 'axios'

    export default {
        name: 'home',
        components: {
            Loader, Map
        },
        data() {
            return {
                users: [],
                info: null,
                loading: true,
                errored: false
            };
        },
        async beforeCreate() {
            // console.log('HOME beforeCreate')
            if (this.$store.getters.getExtUser) {
                if (this.$store.getters.getDbUser) {
                    console.log('DB_USER already set')
                } else {
                    // console.log('DB_USER need to be set')

                    await this.$store.dispatch('GET_USER_BY_EXT_ID', this.$store.getters.getExtUser.id)
                    this.loading = false
                }
            } else {
                console.log('ANONYMOUS USER')
            }
        },
        mounted() {
            if (msgs[this.$route.query.message]) {
                this.$message(msgs[this.$route.query.message])
            }
            //
            // console.log(this.$axios)
            // console.log(axios)
            // console.log(this.$http)
            // console.log(this.$api)

            this.getData()

            // console.log(new Date())
            // console.log(new Date().toUTCString())
            // console.log(new Date().toISOString())
            // console.log(new Date().toLocaleString())
        },
        methods: {
            getData() {
                // console.log('getData')

                // this.interval = setInterval(() => {
                axios.get('https://reqres.in/api/users?delay=1')
                    .then(users => {
                        this.users = users.data.data

                        // console.log(users.data.data)
                    })
                    .catch(error => {
                        console.log(error);
                        this.errored = true;
                    })

                // }, 1000)
            }
        }
    }
</script>
